.avatarLight {
  pointer-events: none;
  position: absolute;
  z-index: 3;
  /* background: url(./images/IMG_05841.JPG); */
  background: linear-gradient(
    30deg,
    rgba(6, 7, 34, 0.082) 0%,
    rgb(88, 151, 245) 19%,
    rgb(26, 28, 109) 68%,
    rgb(214, 50, 50) 100%
  );
  opacity: 0;
  width: 1000px;
  height: 1000px;
  /* animation: movingSpotLight 3.6s ease-in-out alternate infinite; */
  top: 0;
}
@-webkit-keyframes movingSpotLight {
  from {
    -webkit-transform: translateX(-600px);
            transform: translateX(-600px);
    opacity: 0;
    -webkit-filter: saturate(18) brightness(9) blur(180px);
            filter: saturate(18) brightness(9) blur(180px);
  }
  50% {
    opacity: 0.5;
    -webkit-filter: saturate(18) brightness(0.3) blur(180px);
            filter: saturate(18) brightness(0.3) blur(180px);
  }

  to {
    opacity: 0.03;
    -webkit-transform: translateX(600px);
            transform: translateX(600px);
    -webkit-filter: saturate(1) brightness(9) blur(180px);
            filter: saturate(1) brightness(9) blur(180px);
  }
}
@keyframes movingSpotLight {
  from {
    -webkit-transform: translateX(-600px);
            transform: translateX(-600px);
    opacity: 0;
    -webkit-filter: saturate(18) brightness(9) blur(180px);
            filter: saturate(18) brightness(9) blur(180px);
  }
  50% {
    opacity: 0.5;
    -webkit-filter: saturate(18) brightness(0.3) blur(180px);
            filter: saturate(18) brightness(0.3) blur(180px);
  }

  to {
    opacity: 0.03;
