.item-indicators {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: row wrap;
          flex-wrap: row wrap;
}

@media only screen and (max-width: 600px) {
  .app {
    width: 100vw;
    height: 100vh;
    max-height: none;
    max-width: none;
    min-width: 0;
    min-height: 0;
  }
  .nav-wrap {
    width: 57.6px;
  }
  .page-wrap {
    -webkit-flex: 1 1;
            flex: 1 1;
  }
  .avatar-wrap {
    position: absolute;
    left: 0;
    width: 100vw;
    z-index: 1;
  }
  .avatar-text {
    font-size: 3rem;
    margin-top: 0;
    top: 40%;
  }
  .aw-f {
    display: none;
  }
  .footer {
    position: absolute;
    z-index: 999;
    bottom: 0;
    padding: 1rem;
    border-radius: 6px 6px 0 0;
    background-color: #086586;
    background-color: var(--main-color);
    opacity: 0.6;
  }
  .page-title {
    left: 2rem;
    font-size: 2rem;
  }
  .endbar {
    margin-top: 50vh;
  }
  .name-wrap {
    padding: 0;
    font-size: 3rem;
  }
  .name-data {
    border: none;
    padding: 1rem;
  }
  #info-tag {
    width: 100%;
  }
  #info-data {
    border-radius: 0 0 15px 15px;
  }
  .language {
    width: 100%;
  }
  .blogType {
    width: 100%;
  }
  .interest {
    width: 40%;
  }
  .skill-card {
    width: 100%;
  }
  .skill-card-title {
    height: 2rem;
    font-size: 1.2rem;
    margin-bottom: 1rem;
  }
  .skill-card-item {
    font-size: 1rem;
  }
  .item-column {
    width: 100%;
  }
  .item-column.left {
    border-right: none;
  }
  .item-column.right {
    border-left: none;
  }
  .work-card-container {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .work-card-left {
    width: 100%;
    margin-bottom: 1rem;
  }
  .work-card-desc {
    width: 100%;
  }
  .contactMsg {
    font-size: 1.2rem;
    line-height: 2rem;
  }
  p.min-break {
    display: none;
  }
  .copyLabel {
    left: 1.2rem;
    font-size: 0.8rem;
  }
  .copyText {
    width: 100%;
    font-size: 1rem;
    border-radius: 12px;
  }
  .copyButton {
    display: none;
  }
}

